const db = wx.cloud.database()
const bannerns = db.collection('bannerItem')
export default{
	name:'banDetails',
	data(){
		return{
			id:'',
			src:'',
			text:'',
		}
	},
	methods:{
		handleData(id){
			bannerns.where({
				id
			}).get()
			.then(res=>{
				let data = res.data[0]
				this.src = data.src
				this.text = data.text
			})
			.catch(err=>{
				console.log(err)
			})
		}
	},
	onLoad(e) {
		this.id = e.id
		this.handleData(this.id)
	}
}
